import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private theUserIsAuthenticated = true; //inloggen niet nodig
  private theUserId = 'xyz'; //gebruiker

  get userIsAuthenticated(){
    return this.theUserIsAuthenticated;
  }

  get userId(){
    return this.theUserId;
  }
  constructor() { }
  login() {
    this.theUserIsAuthenticated = true;
  }

  logout(){
    this.theUserIsAuthenticated =false;
  }



}
